.hero {
  width: 100%;
  max-width: 1440px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
  padding: 50px 80px;
}

.hero__avatar {
  width: 550px;
  height: 550px;
  border-radius: 50%;
  border: 20px solid #1e1d1d;
  object-fit: cover;
  -webkit-filter: hue-rotate(-10deg);
          filter: hue-rotate(-10deg);
}

.hero__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 1.2;
  margin: 0;
}

.hero__subtitle {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.2;
  margin: 9px 0 35px;
  color: #828282;
}

.hero__list-item {
  margin-right: 10px;
}

.hero__list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 0;
  list-style: none;
  padding: 12px 0;
}

.hero__link {
  padding: 12px 27px;
  color: white;
  text-decoration: none;
  background-color: #27ae60;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  position: relative;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 50px;
  z-index: 100;
}

.hero__link span {
  z-index: 1000;
  text-align: center;
}

.hero__link_circle {
  border-radius: 50%;
  transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 0px;
  width: 0px;
  z-index: 1;
}

.hero__link:hover {
  background-color: #27ae60;
  opacity: 0.8;
}

.hero__link:hover .hero__link_circle {
  height: 250px;
  width: 250px;
}

.hero__list-item:nth-last-of-type(1) {
  margin-right: 0;
}

@media screen and (max-width: 1200px) {
  .hero__avatar {
    width: 400px;
    height: 400px;
  }
  .hero__title {
    font-size: 32px;
  }

  .hero__subtitle {
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  .hero {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
  }
  .hero__avatar {
    width: 300px;
    height: 300px;
    margin-bottom: 35px;
  }
  .hero__title {
    font-size: 32px;
    text-align: center;
  }
  .hero__subtitle {
    font-size: 14px;
    text-align: center;
  }
}

@media screen and (max-width: 425px) {
  .hero {
    padding: 20px;
  }
}
.about {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  max-width: 1440px;
  margin: 0 auto;
}

.about__title {
  font-weight: 700;
  font-size: 48px;
  line-height: 1.2;
  margin: 0;
  max-width: 1440px;
  padding: 30px 0;
}

.about__techs {
  max-width: 745px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  padding: 0;
  margin: 0;
}

.about__techs-list {
  max-width: 745px;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  padding: 38px 0;
  margin: 0;
  list-style: none;
}

.about__techs-list-item {
  padding: 10px 0;
}

.about__my-techs-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  max-width: 1440px;
  width: 70%;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.2;
  padding: 38px 0;
  list-style: none;
  margin: 0;
}

.about__my-techs-list-item {
  margin: 0 10px;
  position: relative;
  width: 10%;
  text-align: center;
}

.about__my-techs-list-text {
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 100%);
          transform: translate(-50%, 100%);
  text-transform: capitalize;
  width: 100%;
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .about__my-techs-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 60px;
    gap: 60px;
  }
}

@media screen and (max-width: 425px) {
  .about {
    padding: 20px;
  }
  .about__my-techs-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 60px;
    gap: 60px;
  }
  .about__title {
    font-size: 32px;
    padding: 0 0 30px;
  }
}

@media screen and (max-width: 375px) {
  .about__my-techs-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 60px;
    gap: 60px;
  }
  .about__techs {
    text-align: center;
  }
  .about__techs-list {
    padding: 0 0 30px;
  }
}

.projects {
  max-width: 1440px;
  margin: 0 auto;
}

.projects__title {
  text-align: center;
  font-weight: 700;
  font-size: 48px;
  line-height: 1.2;
  margin: 0;
  padding: 30px 0;
}

.projects__container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 60px;
  gap: 60px;
  padding: 38px 80px;
}

.projects__project {
  width: 350px; 
  min-height: 240px;
  margin: 0 auto;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
}

.projects__image {
  object-fit: cover;
  width: 100%;
  border-radius: 5px;
}

.project__details {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  top: 50%; 
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  opacity: 0;
  border-radius: 5px;
}

.project__details:hover {
  opacity: 1;
}

.project__details-text {
  text-align: center;
}

.project__details-link, .project__details-text {
  padding: 5px 10px;
  margin: 5px 0;
  color: white; 
  text-decoration: none;
}

.project__details-link {
  border: 1px solid white;
  border-radius: 50px;
}

.project__details-text:nth-last-of-type(1) {
  font-style: italic;
}

.project__details-link:hover {
  background-color: #27AE60;
  border-color: transparent;
}

@media screen and (max-width: 1400px) {
  .projects__container {
    grid-template-columns: repeat(2, 350px);
    -webkit-justify-content: center;
            justify-content: center;
  }
}

@media screen and (max-width: 768px) {
  .projects__container {
    grid-template-columns: repeat(1, 350px);
  }
}

@media screen and (max-width: 425px) {
  .projects {
    padding: 20px;
  }
  .projects__title {
    font-size: 32px;
  }
}


@media screen and (max-width: 375px) {
  .projects__project {
    width: 250px; 
    min-height: 300px;
    margin: 0 auto;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
  }

  .project__details {
    opacity: 1;
  }
}
